import React, {useState} from "react";

export default function TextForm(props) {
  const handleOnChange=(event)=>{
    setText(event.target.value)
  }
  const handleOnUpClick=()=>{
    setText(text.toUpperCase())
  }
  const handleOnLoClick=()=>{
    setText(text.toLowerCase())
  }
  const handleOnCpClick=()=>{
   let a=text.toLowerCase().split(" ")
   let str="";
   a.forEach(element => {
    element=element.charAt(0).toUpperCase()+ element.substring(1,element.length)
    str=str+element.trim()+" ";
   });
   setText(str.trim());
  }
  const [text,setText]=useState('')
  return (
    <>
      <div className="container">
        <div className="my-3">
            <h1 style={{color:props.mode==='dark'?'white':'black'}}>{props.heading}</h1>
          <textarea
            className="form-control"
            id="myBox"
            rows="8" value={text} onChange={handleOnChange}
            style={{color:props.mode==='dark'?'white':'black', backgroundColor:props.mode==='dark'?'grey':'white'}}></textarea>
          <button className="btn btn-primary my-3 mx-1" onClick={handleOnUpClick}>Convert Upper Case</button>
          <button className="btn btn-primary my-3 mx-1" onClick={handleOnLoClick}>Convert Lower Case</button>
          <button className="btn btn-primary my-3 mx-1" onClick={handleOnCpClick}>Capitilization</button>
        </div>
        </div>
        <div className="container">
          <h2 style={{color:props.mode==='dark'?'white':'black'}}>Your Text Summry...</h2>
          <p style={{color:props.mode==='dark'?'white':'black'}}>{(text===""?0:text.split(" ").length)} words and {text.length} characters</p>
          <p style={{color:props.mode==='dark'?'white':'black'}}>you can read in {(0.008*text.split(" ").length).toPrecision(2)} minutes...</p>
          <h1 style={{color:props.mode==='dark'?'white':'black'}}>Preview</h1>
          <p style={{color:props.mode==='dark'?'white':'black'}}>{text}</p>
        </div>
    </>
  );
}
