import React, {useState}from "react";
import PropTypes from 'prop-types'

About.propTypes={heading: PropTypes.string}
About.defaultProps={heading: "Set About Heading"}

export default function About(props) {
    const [btnHeading, setBtnHeading]=useState('Enable Dark Mode')
    const [tabStyle,setTabStyle]=useState({
        backgroundColor: 'white',
        color: 'black',
        border: '1px solid red'
    })
    const toogleClick=()=>{
        if(tabStyle.color==='black')
        {
            setTabStyle({
                backgroundColor: 'black',
                color: 'white',
                border: '1px solid red'
            })
            setBtnHeading("Enable Light Mode")
        }
        else
        {
            setTabStyle({
                backgroundColor: 'white',
                color: 'black',
                border: '1px solid red'
            })
            setBtnHeading("Enable Dark Mode")
        }
    }
  return (
    <>
    <h1>{props.heading}</h1>
    <div className="accordion" id="accordionExample">
      <div className="accordion-item" style={tabStyle}>
        <h2 className="accordion-header">
          <button
            className="accordion-button"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseOne"
            aria-expanded="true"
            aria-controls="collapseOne" style={tabStyle}
          >
            Accordion Item #1
          </button>
        </h2>
        <div
          id="collapseOne"
          className="accordion-collapse collapse show"
          data-bs-parent="#accordionExample" 
        >
          <div className="accordion-body">
            <strong>This is the first item's accordion body.</strong> It is
            shown by default, until the collapse plugin adds the appropriate
            classes that we use to style each element. These classes control the
            overall appearance, as well as the showing and hiding via CSS
            transitions. You can modify any of this with custom CSS or
            overriding our default variables. It's also worth noting that just
            about any HTML can go within the <code>.accordion-body</code>,
            though the transition does limit overflow.
          </div>
        </div>
      </div>
      <div className="accordion-item" style={tabStyle}>
        <h2 className="accordion-header">
          <button
            className="accordion-button collapsed"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseTwo"
            aria-expanded="false"
            aria-controls="collapseTwo" style={tabStyle}
          >
            Accordion Item #2
          </button>
        </h2>
        <div
          id="collapseTwo"
          className="accordion-collapse collapse"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body">
            <strong>This is the second item's accordion body.</strong> It is
            hidden by default, until the collapse plugin adds the appropriate
            classes that we use to style each element. These classes control the
            overall appearance, as well as the showing and hiding via CSS
            transitions. You can modify any of this with custom CSS or
            overriding our default variables. It's also worth noting that just
            about any HTML can go within the <code>.accordion-body</code>,
            though the transition does limit overflow.
          </div>
        </div>
      </div>
      <div className="accordion-item" style={tabStyle}>
        <h2 className="accordion-header">
          <button
            className="accordion-button collapsed"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseThree"
            aria-expanded="false"
            aria-controls="collapseThree" style={tabStyle}
          >
            Accordion Item #3
          </button>
        </h2>
        <div
          id="collapseThree"
          className="accordion-collapse collapse"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body">
            <strong>This is the third item's accordion body.</strong> It is
            hidden by default, until the collapse plugin adds the appropriate
            classes that we use to style each element. These classes control the
            overall appearance, as well as the showing and hiding via CSS
            transitions. You can modify any of this with custom CSS or
            overriding our default variables. It's also worth noting that just
            about any HTML can go within the <code>.accordion-body</code>,
            though the transition does limit overflow.
          </div>
        </div>
      </div>
    </div>
    <button onClick={toogleClick} className="btn btn-primary my-3">{btnHeading}</button>
    </>
  );
}
