import logo from "./logo.svg";
import "./App.css";
import Navbar from "./component/Navbar";
import TextForm from "./component/TextForm";
import About from "./component/About";
import Alert from "./component/Alert";
import React,{useState} from "react"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

function App() {
  const [mode,setMode]=useState('light')
  const [alert,setAlert]=useState(null);
  const showAlert = (message,type)=>{
    setAlert({
      message:message,
      type:type
    })
    setTimeout(() => {
      setAlert(null);
    }, 3000);
  }
  const toggleMode=()=>{
    if(mode ==="light")
    {
      setMode("dark")
      document.body.style.backgroundColor='grey'
      showAlert("Dark mode applied","success")
    }
    else
    {
      setMode("light")
      document.body.style.backgroundColor='white'
      showAlert("Light mode applied","success")
    }
  }
  // const toggleMode=(m)=>{
  //     setMode(m)
  //     document.body.style.backgroundColor=m.target.value
  //     //console.log(m.target.value);
  // }
  return (
    <>
      <Router>
        <Navbar navbarHeading="Text Utils" toggleMode={toggleMode} mode={mode}/>
        <Route exact path="/">
              <TextForm heading="Enter text to analyze..." mode={mode}/>
        </Route>
        <div className="container my-3">
          <Alert alert={alert}/>
          <Switch>
            <Route exact path="/About">
              <About heading="About Us"/>
            </Route>
            <Route exact path="/">
              <TextForm heading="Enter text to analyze..." mode={mode}/>
            </Route>
          </Switch>
        </div>
      </Router>
    </>
  );
}
export default App;
